.widget-area {
    //@include border;
}

.widget {
    
        //@include border(blue);
	margin: 0 0 2em;
	padding: 0 0 2em;
	//font-family: $font__main;
	
	ul,
	ol {
		padding: .7em 0 0;
		margin-bottom: 0;
		list-style-type: none;

		li {
			display: block;
			line-height: 2em;

			ul,
			ol {
				margin-left: .5em;

				@media screen and (min-width: 30em) {
					margin-left: 1em;
				}
			}
			
		}
	}
	
	a {
		//font-weight: 600;
		text-decoration: none;
		
		&:hover,
		&:focus {
			border-bottom: 3px solid orangered;
		}
	}
	
	/* Make sure select elements fit in widgets. */
	select {
		width: 100%;
		height: 2.5em;
		padding: 10px;
		//font-family: $font__main;
		border: 1px solid #ccc;
	}
	
}

.widget-title {
	margin-top: 0;
	margin-bottom: .5em;
	font-size: 1.4em;
}

/* Archive and Categories widgets */
.widget_archive li {
	color: #7D7D7D;
}

.widget_categories .children {
	padding-top: 0;
}

/* Search widget. */
.widget_search .search-submit {
	display: none;
}

/* Pages and Menu widgets */
.widget_pages .children,
.widget_nav_menu .sub-menu {
	padding-top: 0;
	padding-bottom: .5em;
}

.widget_pages,
.widget_nav_menu {
	
	a {
		display: block;
		padding: 1em 0;
		text-decoration: none;
		border-bottom: 1px dashed hsl(0, 0%, 50%);
		
		&:hover,
		&:focus {
			text-decoration: none;
			border-bottom: 1px dashed hsl(0, 0%, 50%);
		}
	}
	
	li {
		padding: 0;

	}
	
}

/* Meta widget */
.widget_meta li {
	padding: 0;
}

/* Recent comments */
.widget_recent_comments {

	li {
		line-height: 1.3em;
		padding-bottom: .5em;
	}
	
	a:hover,
	a:focus {
		padding-left: 0;
	}
	
}

/* RSS widget */
.widget_rss li {
	margin-bottom: 1em;
}
.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rssSummary {
	padding: .5em 0;
	//font-family: $font__header;
	font-size: .8em;
    line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;
	
	caption {
		padding: .5em 0;
		font-size: 1em;
		font-weight: 500;
		text-align: left;
	}
	
	thead {
		background: transparent;
		color: black;
		
		th {
			border-bottom-width: 2px;
		}
	}
	
	td {
		padding: .2em;
		font-size: .8em;
		text-align: center;
		background: white;
		border: 1px solid hsl(0, 0%, 80%);
	}
	
	th {
		border: none;
		text-align: center;
	}
	
	.pad {
		background: #eaeaea;
	}
	
	a {
		font-weight: 900;
		text-decoration: none;
	}
	
}

/* Text widget */

/* MailChimp signup form */
.widget #mc_embed_signup {
    p {
        margin: 0.5em 0 1em;
    }
    .indicates-required {
        display: none;
    }
    .mc-field-group {
        clear:left; 
        position:relative; 
        width:96%; 
        padding-bottom:3%; 
        min-height:50px;
        .asterisk {
            color: red;
        }
    }
    .mc-field-group label {
        display: block;
        font-weight: bold;
        font-size: 85%;
        margin-bottom: 0.25em;
    }
    .mc-field-group input {
        display: block;
        width: 100%;
        padding: 8px 0;
        text-indent: 2%;
    }
    #mce-responses .response {
        margin-bottom: .75em;
    }
}

/* Footer widget styles */

.footer-widgets {
	margin: 2em 0 0;
	padding: 1em 0;
        
	//background-color: hsl(0, 0%, 10%);
        background-color: #ccc;
	
	@media #{$tablet__breakpoint} {
		padding: 2em 1em;
	}
	
	.widget {
		flex: 1;
		margin: 0 1em 1em;
		padding-top: 1em;
		.original-title {
			//color: hsla(0, 0%, 100%, .5);
		}
	}
       
	
	.widget,
	.widget-title,
	.widget a,
	.widget_calendar thead,
	.rss-date, 
	.widget_rss cite {
		//color: white;
	}
	
	.widget_calendar td,
	.widget_calendar a {
		//color: black;
	}
		
	@media #{$desktop__breakpoint} {
		/* Use flex to create a responsive layout */
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		
		/* If only one widget is displayed, make it full width. */
		.widget:only-of-type {
			width: 100%;
		}
                
/*                .widget:nth-child(2) {
                    flex: 2;
                }*/

		/* If 2 or 3 widgets are displayed, make the second one twice as wide. */
		.widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2) { 
			//flex: 2;
                        width: 30%;
			flex: 1 1 30%;
		}
		
		/* If 4 or more widgets are displayed, show three across when available. */
/*		.widget:nth-last-child(n+4), 
		.widget:nth-last-child(n+4) ~ .widget { 
			width: 30%;
			flex: 1 1 30%;
		}*/
	}
	
}
